<!--suppress HtmlUnknownTag, CheckEmptyScriptTag, HtmlUnknownAttribute -->
<template>
  <v-card outlined style="margin: 0 0 10px 0; border-radius: 1em">
    <v-card-title>
      {{name}} &nbsp;
      <span v-if="!!affix" class="suhc-title-affix">({{ affix }})</span>
    </v-card-title>
    <v-card-subtitle>{{walk.date}}</v-card-subtitle>

    <v-expansion-panels :disabled="!expandable">
      <v-expansion-panel hover focusable inset :style="{background: expandable ? 'rgba(255,255,255,0.075)' : null, 'border-radius': '0 0 1em 1em'}">
        <v-expansion-panel-header style="line-height: 1.25">
          {{ fromToVia }}<br>
          {{ attendance }}<br>
          {{ km }} {{ avgMoveSpeed }}
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <p v-if="walk.misc">
            <span v-for="url in walk.misc">
              <a :href="url">{{ urlLabel(url) }}</a>
              <br>
            </span>
          </p>

          <table>
            <tr v-for="h in walk.checkpoints">
              <td class="suhclogtime">{{ h.time }}</td>
              <td>{{ h.event }}</td>
            </tr>
          </table>

          <p v-for="account in walk.accounts"><i><br>{{ account.who }} says... <br></i>{{ account.what }}</p>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </v-card>
</template>

<script>
  export default {
    name: 'SUHCLogEntry',
    props: ['walk'],

    computed: {
      circular () {
        return this.walk.start === this.walk.finish || !this.walk.finish;
      },

      affix () {
        let terms = [];
        if (this.walk.external === 'unofficial' || this.walk.external === 'external')
          terms.push(this.walk.external);
        if (this.walk.unattended)
          terms.push('unattended');
        return terms.join(', ');
      },

      name() {
        return this.walk.name || this.circular && 'Day walk around ' + this.walk.start || 'Day walk from ' + this.walk.start + ' to ' + this.walk.finish;
      },

      fromTo () {
        return this.walk.start &&
          (this.circular && 'Around ' + this.walk.start || 'From ' + this.walk.start + ' to ' + this.walk.finish)
          || '';
      },

      fromToVia () {
        return this.walk.via &&
          ((this.fromTo && this.fromTo + ' via ' || 'Via ') + this.walk.via)
          || this.fromTo;
      },

      attendance () {
        return this.walk.attendanceOverride
            || this.headCount + this.committeeCount;
      },

      headCount () {
        return this.walk.headCount && this.walk.headCount + ' attended' || '';
      },

      committeeCount () {
        return this.headCount && this.walk.committeeCount && ' (' + this.walk.committeeCount + ' committee)' || '';
      },

      km () {
        return this.walk.km && 'Effective walk length: ' + this.walk.km + ' km' || '';
      },

      avgMoveSpeed () {
        return this.walk.avgMoveSpeed && 'at ' + this.walk.avgMoveSpeed;
      },

      expandable () {
        return this.walk.misc || this.walk.checkpoints || this.walk.accounts;
      }
    },

    methods: {
      urlLabel(url) {
        return url.startsWith('https://teonistor-herokuapp-prod.s3.eu-west-2.amazonaws.com/data/maps/') && 'Basic tracker map'
            || url.startsWith('https://www.strava.com') && 'Strava'
            || 'Other link';
      }
    }
  }
</script>
