<!--suppress HtmlUnknownTag, CheckEmptyScriptTag, HtmlUnknownAttribute -->
<template>
  <div>
    <h3>SUHC Captain's Log</h3>

    <p style="text-align: justify">
     I held the position of Day Walk Secretary of SUHC roughly between March 2017 and May 2018. The idea of keeping a (sometimes
     unnecessarily detailed) log of our events occurred to me before that and spans beyond my time on the committee, or in uni,
     and even beyond strictly club events.</p>

    <v-progress-circular v-if="!!restCallsGoing" indeterminate style="margin: 10px 150px" />
    <Notifications :alerts="alerts" />

    <v-expansion-panels v-if="!restCallsGoing && walks.length" class="suhc-filter">
      <v-expansion-panel hover focusable inset>
        <v-expansion-panel-header>
          Filters
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-switch v-model="filter.official" />
            Official – fully fledged events run by the club committee <br>
          <v-switch v-model="filter.unofficial" />
            Unofficial – events run by some club members, advertised on some club comms, but not backed by the committee <br>
          <v-switch v-model="filter.external" />
            External – events having next to nothing to do with the club but which I felt deserve to be on the list <hr>
          <v-switch v-model="filter.attended" />
            Attended – I was personally there and recorded (most of) the log <br>
          <v-switch v-model="filter.unattended" />
            Unattended – I was not there but obtained the log from kind contributors or just felt the event deserves to be on the list

        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <SUHCLogEntry v-for="walk, i in walks"
                  v-if="evalFilter(walk)"
                  :walk="walk"
                  :key="i"
                  :index="i" />
  </div>
</template>

<script>
  import AxiosHelper from '../components/AxiosHelper.vue';
  import Notifications from '../components/Notifications.vue';
  import SUHCLogEntry from '../components/SUHC-LogEntry.vue';

  export default {
    name: 'SUHC_Log',
    components: {
      Notifications,
      SUHCLogEntry
    },
    mixins: [AxiosHelper],

    data: () => ({

      filter: {
        official: true,
        unofficial: true,
        external: true,
        attended: true,
        unattended: true
      },

      walks: []
    }),

    methods: {
      evalFilter (walk) {
        let officialProp = (walk.external === 'unofficial' && 'unofficial')
                        || (walk.external === 'external' && 'external')
                        || 'official';
        let attendedProp = walk.unattended && 'unattended' || 'attended';
        return this.filter[officialProp] && this.filter[attendedProp];
      }
    },

    mounted () {
      this.plainAxios('retrieving log data', axios => axios.get(this.$dataUrl + '/suhc/log.json'), data => this.walks = data);
    }
  }
</script>

<style>
  .suhc-title-affix {
    opacity: 0.55;
  }

  .suhc-filter {
    padding: 0 20px 20px;
  }

  .suhc-filter .v-input {
    display: inline-block;
    vertical-align: middle;
  }

  td.suhclogtime {
    text-align: center;
    vertical-align: top;
    padding-right: 1em;
  }
</style>
